import {applyEffectHandler} from "./effectsHandler.js"

export class basicBuff{
static generatedEffect = "buffs"

  constructor(name,duration , uses){
    this.name = name

    this.duration = duration ?? "inf"
    this.uses = uses ?? "ind"
  }
 
  apply(entity){
    this.init(entity)
  }
  remove(){
    this.defaultRemove()
  }

  defaultInit(entity){
// for when first applied
    entity.buffs[this.name] = this
    this.hostingEntity = entity
  }
  effect(){
// subs to pubsub event and triggers
  }
  defaultRemove(){
// for removal
    delete this.hostingEntity.buffs[this.name]
    delete this
  }

}