import {applyEffectHandler , reduceEffectDuration , reduceEffectUses} from "./effectsHandler.js"
import pubsub from "../pubsub.js"

export class basicDebuff{
static generatedEffect = "debuffs"

  constructor(name , duration , uses){
    this.name = name

    this.duration = duration ?? "inf"
    this.uses = uses ?? "ind"
  }

  apply(entity){
    this.init(entity)
  }
  remove(){
    this.defaultRemove()
  }

  defaultInit(entity){
// for when first applied
    applyEffectHandler(this,entity)
  }
  effect(){
// subs to pubsub event and triggers
  }
  defaultRemove(){
// for removal
    delete this.hostingEntity.debuffs[this.name]
    delete this
  }
}


export class vulnerable extends basicDebuff{
  constructor(duration){
    super("Vulnurable" , duration , "inf" )
  }

  init(entity){
    this.defaultInit(entity)
    pubsub.on("turnStarted" , this)

    entity.defMod += 0.5
  }


  effect(entity){
    reduceEffectDuration(this)
  }

  remove(){
    this.hostingEntity.defMod -= 0.5
    pubsub.off("turnStarted" , this)
    this.defaultRemove()
  }
}



